	module.exports = {"data":{"markdownRemark":{"html":"<p>A great way to stimulate your Local Group is by asking questions.</p>\n<p><strong>You can ask questions like</strong></p>\n<ul>\n<li>What is everyone working on this week?</li>\n<li>How did you approach a specific Challenge, Algorithm, Front End Project, Back End Project?</li>\n<li>Would anyone be interested in meeting up to pair program together this week?</li>\n<li>Did everyone hear about “insert recent announcement”</li>\n<li>What are some parts of free code camp you’d like to give additional focus to?</li>\n</ul>\n<p><strong>You can also provide useful links for other members to relevant resources that you find helpful.</strong></p>","fields":{"slug":"/miscellaneous/how-to-stimulate-your-facebook-campsite/"},"frontmatter":{"title":"How to Stimulate Your Facebook Campsite"}}},"pathContext":{"slug":"/miscellaneous/how-to-stimulate-your-facebook-campsite/"}}

